"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import { FaSave, FaTimes } from "react-icons/fa";
import { motion } from "framer-motion";

export default function ProductSettings({ params }: { params: { category: string } }) {
  const { category } = params; // Dynamic category from URL
  const router = useRouter();

  // State for product details
  const [product, setProduct] = useState({
    id: Date.now(), // Unique ID for the product
    name: "",
    quantity: 0,
    price: 0,
  });

  // State for theme (light/dark)
  const [theme, setTheme] = useState<"light" | "dark">("light");

  // State for success/error messages
  const [message, setMessage] = useState("");

  // Handle form input changes
  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setProduct({ ...product, [name]: value });
  };

  // Handle form submission
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();

    // Validate inputs
    if (!product.name || product.quantity < 0 || product.price < 0) {
      setMessage("Please fill in all fields correctly.");
      return;
    }

    try {
      // Fetch existing products
      const response = await fetch("/data/products.json");
      const data = await response.json();

      // Add new product to the category
      data[category].push(product);

      // Save updated data back to the JSON file (simulate API call)
      await fetch("/api/updateProducts", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      });

      setMessage("Product updated successfully!");
      setProduct({ id: Date.now(), name: "", quantity: 0, price: 0 }); // Reset form
      router.refresh(); // Refresh the page to reflect changes
    } catch (error) {
      setMessage("An error occurred. Please try again.");
      console.error("Error updating product:", error);
    }
  };

  // Toggle theme
  const toggleTheme = () => {
    setTheme((prevTheme) => (prevTheme === "light" ? "dark" : "light"));
  };

  // Apply theme to the body
  useEffect(() => {
    document.body.className = theme === "dark" ? "bg-gray-900 text-white" : "bg-gray-100 text-gray-900";
  }, [theme]);

  return (
    <div className={`min-h-screen p-8 transition-colors duration-300 ${theme === "dark" ? "bg-gray-900" : "bg-gray-100"}`}>
      <div className={`max-w-2xl mx-auto p-8 rounded-lg shadow-sm border transition-colors duration-300 ${theme === "dark" ? "bg-gray-800 border-gray-700" : "bg-white border-gray-100"}`}>
        {/* Theme Toggle Button */}
        <button
          onClick={toggleTheme}
          className={`absolute top-4 right-4 p-2 rounded-full transition-colors duration-300 ${theme === "dark" ? "bg-gray-700 hover:bg-gray-600" : "bg-gray-200 hover:bg-gray-300"}`}
        >
          {theme === "dark" ? "üåô" : "‚òÄÔ∏è"}
        </button>

        <motion.h1
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-3xl font-bold mb-6"
        >
          {category} Settings
        </motion.h1>

        <p className={`text-lg mb-8 transition-colors duration-300 ${theme === "dark" ? "text-gray-300" : "text-gray-600"}`}>
          Update {category.toLowerCase()} details such as name, quantity, and price.
        </p>

        {/* Success/Error Message */}
        {message && (
          <motion.div
            initial={{ opacity: 0, y: -10 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.3 }}
            className={`p-4 mb-6 rounded-lg transition-colors duration-300 ${
              message.includes("successfully")
                ? theme === "dark"
                  ? "bg-green-800 text-green-200"
                  : "bg-green-100 text-green-700"
                : theme === "dark"
                ? "bg-red-800 text-red-200"
                : "bg-red-100 text-red-700"
            }`}
          >
            {message}
          </motion.div>
        )}

        {/* Product Update Form */}
        <motion.form
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          onSubmit={handleSubmit}
          className="space-y-6"
        >
          <div>
            <label htmlFor="name" className={`block text-sm font-medium transition-colors duration-300 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}>
              Product Name
            </label>
            <input
              type="text"
              id="name"
              name="name"
              value={product.name}
              onChange={handleInputChange}
              className={`mt-1 block w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500 transition-colors duration-300 ${
                theme === "dark"
                  ? "bg-gray-700 border-gray-600 text-white"
                  : "bg-white border-gray-300 text-gray-900"
              }`}
              required
            />
          </div>

          <div>
            <label htmlFor="quantity" className={`block text-sm font-medium transition-colors duration-300 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}>
              Quantity
            </label>
            <input
              type="number"
              id="quantity"
              name="quantity"
              value={product.quantity}
              onChange={handleInputChange}
              className={`mt-1 block w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500 transition-colors duration-300 ${
                theme === "dark"
                  ? "bg-gray-700 border-gray-600 text-white"
                  : "bg-white border-gray-300 text-gray-900"
              }`}
              min="0"
              required
            />
          </div>

          <div>
            <label htmlFor="price" className={`block text-sm font-medium transition-colors duration-300 ${theme === "dark" ? "text-gray-300" : "text-gray-700"}`}>
              Price
            </label>
            <input
              type="number"
              id="price"
              name="price"
              value={product.price}
              onChange={handleInputChange}
              className={`mt-1 block w-full p-2 border rounded-md focus:ring-2 focus:ring-blue-500 transition-colors duration-300 ${
                theme === "dark"
                  ? "bg-gray-700 border-gray-600 text-white"
                  : "bg-white border-gray-300 text-gray-900"
              }`}
              min="0"
              step="0.01"
              required
            />
          </div>

          {/* Form Buttons */}
          <div className="flex justify-end gap-4">
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              type="button"
              onClick={() => router.push(`/Components/Products/${category}`)}
              className={`flex items-center gap-2 px-4 py-2 rounded-md transition-colors duration-300 ${
                theme === "dark"
                  ? "bg-gray-700 text-white hover:bg-gray-600"
                  : "bg-gray-200 text-gray-900 hover:bg-gray-300"
              }`}
            >
              <FaTimes /> Cancel
            </motion.button>
            <motion.button
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              type="submit"
              className={`flex items-center gap-2 px-4 py-2 rounded-md transition-colors duration-300 ${
                theme === "dark"
                  ? "bg-blue-700 text-white hover:bg-blue-600"
                  : "bg-blue-500 text-white hover:bg-blue-600"
              }`}
            >
              <FaSave /> Save Changes
            </motion.button>
          </div>
        </motion.form>
      </div>
    </div>
  );
}